<template>
  <div id="app">
    
    <!-- Header -->
    <app-header/>

    <!-- Routher View -->
    <transition
      :name="shouldTransition ? 'fade' : ''"
      mode="out-in">

      <router-view :key="$route.name"></router-view>
    </transition>

    <!-- Footer -->
    <app-footer/>

  </div>
</template>

<script>
import Header from '@/components/Header'
import Footer from '@/components/Footer'
import store from './store'

// Transition   $router.name 
const exceptTransitionList = ['about'];
export default {
  store,
  name: "App",
  components: {
    'app-header': Header, 
    'app-footer': Footer
  },
  data: function() {
    return {
      shouldTransition: true
    }
  },
  watch: {
    $route (to, from) {
      if (to.name == 'home') {
		$('body').addClass('overflowHidden')
	  } else {
		$('body').removeClass('overflowHidden')
	  }

      if (to.name != 'portfolio detail') {
        var windowHeight = window.innerHeight;
        var metaHeight = windowHeight - 120;
        $('#portfolio-meta').css({height: metaHeight});
      }

      if (exceptTransitionList.includes(to.name)) {
        this.shouldTransition = false;
      } else {
        this.shouldTransition = true;
      }
    }
  }
};
</script>

